<template>
  <div class="secunity">
    <div class="secunity-top">
      <van-nav-bar title="安全中心" left-text="返回" left-arrow @click-left="goback" />
    </div>
    <div class="secunitybox">
      <div class="secunitybox-body">
        <div class="listbox">
          <div class="listboxItem" is-link @click="showpwpopup">
            <span>修改密码</span>
            <van-icon name="arrow" size="20" color="#3f454b" />
          </div>
          <van-popup
            closeable
            position="bottom"
            close-icon-position="top-right"
            round
            :style="{ height: '40%' }"
            v-model="changenum"
          >
            <div class="changepopup">
              <p>修改密码</p>
              <van-form>
                <van-field
                  v-for="(item,index) in Allpassword"
                  :key="index"
                  v-model="item.password"
                  :type="item.isPassword?'text':'password'"
                  :right-icon="item.isPassword?'eye-o':'closed-eye'"
                  placeholder="请输入6-16位字母开头的密码"
                  @click-right-icon="showeye(index)"
                  :label="item.title"
                />

                <div style="margin-top: 16px;">
                  <van-button
                    round
                    block
                    color="#3a63f1"
                    native-type="submit"
                    @click="changepw"
                  >确认修改</van-button>
                </div>
              </van-form>
            </div>
          </van-popup>
          <div class="listboxItem" @click="logout">
            <span>注销账号</span>
            <van-icon name="arrow" size="20" color="#3f454b" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/security.less";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      changenum: false,
      Allpassword: {
        old: {
          password: "",
          title: "旧密码",
          isPassword: false,
        },
        new: {
          password: "",
          title: "新密码",
          isPassword: false,
        },
      },
    };
  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
    //点击修改密码显示popup
    showpwpopup() {
      this.changenum = !this.changenum;
    },
    // 显示密码
    showeye(index) {
      this.Allpassword[index].isPassword = !this.Allpassword[index].isPassword;
    },
    //修改密码函数
    changepw() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      //验证表单

      let o = {
        oldPassword: {
          value: this.Allpassword.old.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errMsg: "旧密码需数字与字母组合",
        },
        newPassword: {
          value: this.Allpassword.new.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errMsg: "密码需数字与字母组合",
        },
      };

      if (!validForm.valid(o)) {
        return;
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oldPassword: this.Allpassword.old.password,
        password: this.Allpassword.new.password,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/updatePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "E003") {
            this.$notify({ type: "warning", message: result.data.msg });
          }
          if (result.data.code == "E001") {
            this.$notify({ type: "success", message: result.data.msg });
            localStorage.removeItem("CSTK");
            this.$router.push("/login");
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //注销账号
    logout() {
      this.$dialog
        .confirm({
          title: "确认注销账号?",
          // message: "确认注销账号",
        })
        .then(() => {
          let tokenString = localStorage.getItem("CSTK");
          if (!tokenString) {
            return this.$router.push("/login");
          }
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
          });
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });
          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data,
          })
            .then((result) => {
              this.$toast.clear();

              if (result.data.code == "G001") {
                this.$toast(result.data.msg);
                localStorage.removeItem("CSTK");
                setTimeout(() => {
                  this.$router.push("/login");
                }, 600);
              }
            })
            .catch((err) => {
              this.$toast.clear();
            });
        })
        .catch((err) => {
          this.$toast("注销失败");
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>